<template>
  <div class="login">
    <div class="login-top">
      <van-nav-bar>
        <template #left>
          <div class="login-left">
            <div class="icon">
              <img class="auto-img" src="../assets/images/icon_luckin_coffee.png" alt />
            </div>
            <div class="text">Luckin Coffee</div>
          </div>
        </template>
        <template #right>
          <router-link to="main/Home"><div class="login-right">关闭</div></router-link>
        </template>
      </van-nav-bar>
    </div>


    <div class="login-form">
      <h1>欢迎回来！</h1>
      <p>Please login to your accounts</p>

      <van-form>
        <van-field v-model="userLoginInfo.phone" placeholder="手机号" />
        <van-field
          v-model="userLoginInfo.password"
          :type="isPassword ? 'password' : 'text'"
          placeholder="密码"
          :right-icon="isPassword ? 'closed-eye' : 'eye-o'"
          @click-right-icon="changePassword"
        />
         <div class="forgot" @click="goForgot">忘记密码</div>
        <div class="submit">
          <van-button round block @click="login">登&nbsp;&nbsp;录</van-button>
        </div>
      </van-form>

      <van-cell is-link @click="showPopup" class="registered">注&nbsp; &nbsp;册</van-cell>
      <!-- 自定义图标 -->
      <van-popup
        v-model="show"
        closeable
        position="bottom"
        close-icon-position="top-right"
        :style="{ height: '60%' }"
        round
      >

        <div class="registered-content">
             <h2>注册</h2>
        <van-form>
          <van-field
            v-model="userRegisterInfo.phone"
            placeholder="手机号  中国 (+86)"
          />
           <van-field
            v-model="userRegisterInfo.nickName"
            placeholder="昵称"
          />
          <van-field
          v-model="userRegisterInfo.password"
          :type="isRepassword ? 'password' : 'text'"
          placeholder="密码"
          :right-icon="isRepassword ? 'closed-eye' : 'eye-o'"
          @click-right-icon="changePassword"
        />
          
          <div class="registeredBtn">
            <van-button round block type="info" native-type="submit"  @click="register">注&nbsp; &nbsp;册</van-button>
          </div>
        </van-form>
        </div>
      </van-popup>
    </div>
  </div>
</template>

<script>
import "../assets/less/login.less";
import {validForm} from '../assets/js/validForm'
import {utils} from '../assets/js/utils'

export default {
  data() {
    return {
      //登录信息
      userLoginInfo: {
        phone: '',
        password: '',
      },
      //注册信息
      userRegisterInfo: {
        phone: '',
        nickName: '',
        password: ''
      },

      isPassword: true,
      isRepassword:true,
      show: false
    };
  },
  methods: {
    //查看密码
    changePassword() {
      this.isPassword = !this.isPassword;
       this.isRepassword = !this.isRepassword;
    },

    //弹出注册面板
    showPopup() {
      this.show = true;
    },

    //注册
    register(){
      //获取用户注册信息
      let userInfo = this.userRegisterInfo;
      // 
      let o = {
        phone: {
          value: userInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: '手机号格式不正确'
        },
        nickName:{
          value: userInfo.nickName,
          reg: /^[\w\u4e00-\u9fa5]{1,10}$/,
          errorMsg: '昵称格式不正确'
        },
        password: {
          value: userInfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: '密码格式不正确'
        }
      };

      if(!validForm.valid(o)){
        //验证不通过
        return;
      }

      //发起请求
      //开启加载提示
      this.$toast.loading({
         message: '加载中...',
         //防止穿透
         forbidClick: true,
        //  显示时间，如果时间为0，则不会自动关闭
         duration: 0,

         loadingType: 'spinner',
      })

      //序列化参数
      //复制对象
      let info = Object.assign({},userInfo);
      info.appkey = this.appkey;
      let data = utils.queryString(info);
      
      

      this.axios({
        method: 'POST',
        //请求参数
        url: '/register',
        data
      }).then(result =>{

        //关闭加载提示
        this.$toast.clear();
        

        //如果注册成功
        if(result.data.code == 100){
          this.show = false;

          for(let key in userInfo){
            userInfo[key] = '';
          }
         
          this.$notify({
              type:'success',
              message:result.data.msg
            })
        } else{

            //如果注册失败，则提示
            this.$notify({
              type:'danger',
              message:result.data.msg
            })
          }
      }).catch(err =>{
        this.$toast.clear();
        
      })
    },

    
    //登录
    login() {
      let userInfo = this.userLoginInfo;

      let o = {
        phone: {
          value: userInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: '手机号格式不正确'
        },
        nickName:{
          value: userInfo.nickName,
          reg: /^[\w\u4e00-\u9fa5]{1,10}$/,
          errorMsg: '昵称格式不正确'
        },
        password: {
          value: userInfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: '密码格式不正确'
        }
      };

        if(!validForm.valid(o)){
        //验证不通过
        return;
      }

      //发起请求
      //开启加载提示
      this.$toast.loading({
         message: '加载中...',
         //防止穿透
         forbidClick: true,
        //  显示时间，如果时间为0，则不会自动关闭
         duration: 0,

         loadingType: 'spinner',
      })

      //序列化参数
      //复制对象
      let info = Object.assign({},userInfo);
      info.appkey = this.appkey;
      let data = utils.queryString(info);
      
      

      this.axios({
        method:'POST',
        //请求参数
        url:'/login',
        data
      }).then(result =>{

        //关闭加载提示
        this.$toast.clear();
        

        //如果登录成功
        if(result.data.code == 200){

          localStorage.setItem('CSTK',result.data.token);
          this.$router.push({name:'Home'});
          for(let key in userInfo){
            userInfo[key] = '';
          }
         
          this.$notify({
              type:'success',
              message:result.data.msg
            })
        } else{

            //如果登录失败，则提示
            this.$notify({
              type:'danger',
              message:result.data.msg
            })
          }
      }).catch(err =>{
        this.$toast.clear();
        
      })
    },

    //忘记密码
    goForgot(){
      this.$router.push({name:'Forgot'});
    }
  }
};
</script>

<style lang="less" scoped>
</style>